@import '../../assets/styles/main';

.card {
    height: 100vh;
    @include flex(column);
    color: white;

    &:nth-child(odd) { background: $dark-grey; }
    &:nth-child(even) { background: lighten($dark-grey, 5); }

    .question {
        margin: 1.3rem 0 2rem;
    }

    .questionNumber {
        margin-bottom: 0.8rem;
        font-size: 0.9rem;
        color: $blue;
    }

    .questionImage {
        width: 30rem;
    }

    .next, .finish {
        font-size: 1rem;
        margin-top: 1.8rem;
        padding: 0.6rem 1.5rem;
        color: white;
        border: none;
        border-radius: 9px;
        -webkit-border-radius: 9px;
        -moz-border-radius: 9px;
        -ms-border-radius: 9px;
        -o-border-radius: 9px;
    }

    .next {
        background: $semi-dark-blue;

        &:hover {
            background: lighten($semi-dark-blue, 3)
        }
    }

    .finish {
        background: $blue-green;

        &:hover {
            background: lighten($blue-green, 3)
        }
    }

    .disabled {
        background: lighten($grey, 3);

        &:hover {
            background: lighten($grey, 3);
        }
    }

    .answers {
        button {
            cursor: pointer;
            margin: 0 0.3rem;
            background: none;
            border: 2px solid white;
            color: white;
            width: 10rem;
            height: 5rem;
            border-radius: 9px;
            -webkit-border-radius: 9px;
            -moz-border-radius: 9px;
            -ms-border-radius: 9px;
            -o-border-radius: 9px;

            &:hover {
                background: #fff1;
            }
        }

        .selected {
            background: $gradient;
            border: none;

            &:hover {
                background: $gradient;
            }
        }
    }
}

@media(max-width: 780px) {
    .card {
        .question {
            margin: 1.3rem 0 2rem;
        }
    
        .questionImage {
            width: 60vw;
        }
    
        .answers {
            button {
                margin: 0.3rem 0.3rem 0.3rem 0.6rem;
                width: 45vw;
                height: 13vh;
            }
        }
    }
}

@media(max-width: 580px) {
    .card {
        .questionImage {
            width: 85vw;
        }
    }
}
